.complete-tasks-list-container{

    width: 100%;
    height: 40%;
    max-width: 619px;
   /*  max-height: 400px; */
    overflow-y: hidden;
    
}

.complete-tasks-list-container .todo-list-container{
    width: 100%;
    height: 100%;
    background-color: rgba(220, 232, 243, 0);
   
}

.complete-tasks-list-container .todo-list-container ul{
    
    height: auto;
    /* background-color: aqua; */
    /* display: none; */
   
}

.complete-tasks-list-container .task{
    
    text-decoration:line-through;
    text-decoration-color: rgb(243, 244, 245);
    text-decoration-thickness: 2px;
    
    
}

.complete-tasks-list-container .task .task-quit{
    
    display: none;

}

.complete-tasks-list-container .task .task-check{
    
    display: none;

}

